import React from 'react';
import { Dialog } from './Dialog';
import { Card } from '../Card';
import { Button } from '../Button';
import { typographyStyles } from '../../styles/common-styles';
import classNames from 'classnames';

export interface DialogConfirmationProps {
  readonly isOpen: boolean;
  readonly onClose: (value: boolean) => void;
  readonly onConfirm: () => void;
  readonly children: {
    titleText: string;
    subtitleText?: string;
    cancelText: string;
    confirmationText: string;
  };
}
export const DialogConfirmation: React.FC<DialogConfirmationProps> = ({
  isOpen,
  onClose,
  onConfirm,
  children,
}) => {
  return (
    <Dialog isOpen={isOpen} onClose={onClose} showOverlay={true}>
      <Card className="w-80 text-center z-10">
        <h1 className={typographyStyles.modalH1}>{children.titleText}</h1>
        <p className={classNames(typographyStyles.modalBody1, 'my-6')}>{children.subtitleText}</p>
        <div className="flex gap-x-6">
          <Button
            variant="outlined"
            color="inverseSubtle"
            className="flex-1"
            onClick={() => onClose(false)}
          >
            {children.cancelText}
          </Button>
          <Button variant="filled" color="accent" className="flex-1" onClick={() => onConfirm()}>
            {children.confirmationText}
          </Button>
        </div>
      </Card>
    </Dialog>
  );
};
